<template>
  <v-app id="starWar" >
    <v-navigation-drawer permanent
      overflow
      app
      stateless
      
    >

      <v-list class="list-position">
      <v-img
        src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/Star_wars.png?sign=1a92e2f0204dca1f8d09da4ee8531a16&t=1554027236"
      />    

    <hr class="mt-3 v-divider theme--light" >

        
            <v-list-tile
              avatar
              ripple
              @click="selectPath('starWarPreface')"
              class="v-list__tile--avatar"
            >
              
                  <v-icon class="v-icon-position">details</v-icon>
                  <v-list-tile-title  class="v-title-position">前言</v-list-tile-title>
             
            </v-list-tile>
       

      <v-list-group
        prepend-icon="account_circle"
       
      >
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-title>正派</v-list-tile-title>
          </v-list-tile>
        </template>
        
          <v-list-tile
             v-for="item in itemsHero"
            :key="item.text"      
            avatar
            ripple
            @click="selectPath(item.name)"
          >
            
                <v-list-tile-avatar size="36" class="avatar-position ">
                    <v-img  :src="item.avatar" />
                  </v-list-tile-avatar>
             
                  <v-list-tile-content class="content-position-starWar">
                    <v-list-tile-title  v-text="item.text"></v-list-tile-title>
                  </v-list-tile-content>
            
         </v-list-tile>   
      
      </v-list-group>

      <v-list-group
        prepend-icon="whatshot"
         
      >
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-title>反派</v-list-tile-title>
          </v-list-tile>
        </template>
          <v-list-tile
             v-for="item in itemsVillain"
            :key="item.text"      
            avatar
            ripple
            @click="selectPath(item.name)"
          >
            
                <v-list-tile-avatar size="36" class="avatar-position">
                    <v-img  :src="item.avatar" />
                  </v-list-tile-avatar>
             
                  <v-list-tile-content class="content-position">
                    <v-list-tile-title  v-text="item.text"></v-list-tile-title>
                  </v-list-tile-content>
            
             
         </v-list-tile>
      </v-list-group>

        

      
      <v-list-group
        prepend-icon="group"
       
      >
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-title>组织</v-list-tile-title>
          </v-list-tile>
        </template>
      

          <v-list-tile
             v-for="item in  itemsOrganizations"
            :key="item.text"      
            avatar
            ripple
            @click="selectPath(item.name)"
          >
            
                <v-list-tile-avatar size="36" class="avatar-position">
                    <v-img  :src="item.avatar" />
                  </v-list-tile-avatar>
             
                  <v-list-tile-content class="content-position">
                    <v-list-tile-title  v-text="item.text"></v-list-tile-title>
                  </v-list-tile-content> 
         </v-list-tile>
      </v-list-group>


    
          <v-list-tile
            avatar
            ripple
            @click="selectPath('starWarMovie')" 
            class="v-list__tile--avatar" 
          >
            
            <v-icon class="v-icon-position">local_movies</v-icon>
            <v-list-tile-title  class="v-title-position">电影</v-list-tile-title>
          </v-list-tile>
      
          <v-list-tile
            avatar
            ripple
            @click="selectPath('starWarCartoon')"
            class="v-list__tile--avatar"
          >
            <v-icon class="v-icon-position">live_tv</v-icon>
            <v-list-tile-title  class="v-title-position">卡通</v-list-tile-title>
          </v-list-tile>
     

       

        

       
      </v-list>


    </v-navigation-drawer>


    <v-navigation-drawer permanent
      overflow
      app
      right
      width="130"
      style="text-align:center;margin-top:30px;"
    >
          <div style="display:inline-block">
             
                
                <div>
                <a href="https://www.zhihu.com/people/xiao-shen-18-15/activities" target="_blank"  >
                  <v-list-tile-avatar size="60" class="avatar-position-right ">
                     <v-img src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/zhihu-logo.JPG?sign=8b6c5e3346a0812798409d95d4f1ff7a&t=1554821983" />
                  </v-list-tile-avatar>
                </a>
                </div>
                <span class="text-position-right">知乎</span>
                

                <div>
                <a href="http://space.bilibili.com/166498086?" target="_blank" >
                  <v-list-tile-avatar size="60" class="avatar-position-right ">
                     <v-img src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/bilibili-logo.jpg?sign=af4faf6b682162fecf86922c1a9bcd7a&t=1554821959" />
                  </v-list-tile-avatar>
                </a>
                </div>
                <span class="text-position-right">bilibili</span>  

                 <div>
                <a href="https://github.com/NeighborSpidey" target="_blank"   >
                  <v-list-tile-avatar size="60" class="avatar-position-right ">
                     <v-img src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/github-logo.jpg?sign=3248fcf31e2cfe4c8c4a043300ceca25&t=1554822247" />
                  </v-list-tile-avatar>
                </a>
              </div>
                <span class="text-position-right">Github</span>

                 <v-dialog
           v-model="dialog"
            width="470"
          >
                <template v-slot:activator="{ on }">
                   <div style="cursor:pointer;"  v-on="on">
                    <v-list-tile-avatar size="60" class="avatar-position-right " >
                      <v-img   src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/share.png?sign=a22b73023fbea85b7f4f4b650b346ff3&t=1545211812" />
                    </v-list-tile-avatar>
                   </div>
                <span class="text-position-right">小程序</span>
                </template>
                <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
         
          微信小程序——小深眼中的英雄宇宙
        </v-card-title>

        <v-card-text>
           <img src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/share.png?sign=a22b73023fbea85b7f4f4b650b346ff3&t=1545211812"/>
          <br>
          <span style="font-size:20px;">扫一扫小程序码或微信搜索“小深眼中的英雄宇宙”</span>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            flat
            @click="dialog =false"
          >
            关闭
          </v-btn>
        </v-card-actions>
      </v-card>
                </v-dialog>
                
              </div> 
    </v-navigation-drawer>

    <v-toolbar clipped-left
              clipped-right
              app 
              dark>
   
    
   <a class="logo" href="/" style="display:flex;flex-direction:row">
        <img 
        width=50
        height=50
        src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/img/webLogo.png?sign=94864dc9020544a49ab2194e9c79be13&t=1555404548"/>
        <v-toolbar-title style="margin-left:10px;margin-top:10px;">英雄宇宙</v-toolbar-title>
    </a>
    
    <v-spacer></v-spacer>
    
   
       <v-autocomplete 
        :items="itemsHero.concat(itemsVillain)"
        label="你在寻找哪位超级英雄或反派?"    
        cache-items
        class="mx-3"
        hide-details
        solo-inverted
       >
          <template slot="item" slot-scope="data">
            <v-list-tile v-bind="data.tile.props" v-on="data.tile.on" >
               <v-list-tile-title @click="selectPath(data.item.name)">
                  {{ data.item.text }}
               </v-list-tile-title>
            </v-list-tile>
          </template>
        </v-autocomplete>
   
<v-spacer></v-spacer>

         <v-toolbar-items 
         class="hidden-sm-and-down"
          v-for="bar in itemsBar"
          :key="bar.text"    
         
         >

      <v-btn flat @click="selectBar(bar.name)">{{bar.text}}</v-btn>

    </v-toolbar-items>

    </v-toolbar>

    
    <v-content>
      <v-container fluid> 
           <router-view></router-view>
      </v-container>
    </v-content>
      
    <v-footer :inset="footer.inset" app dark>
      <span class="px-3" style="font-size:16px;">作者：Spidey小深&nbsp;&nbsp;&nbsp;&nbsp;&copy; {{ new Date().getFullYear() }}</span>
    </v-footer>

    
  </v-app>

</template>

