          { name:'hanSolo',text: '汉·索罗',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/104px-Han_Solo_depicted_in_promotional_image_for_Star_Wars_(1977).jpg?sign=b6fa8d3831abd9c5cbdcf74ace1f358d&t=1554278178' },
