          { name:'princessLeia',text: '莱娅公主',avatar: "https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/98px-Princess_Leia's_characteristic_hairstyle.jpg?sign=c70c4ed07c8bae437b5393f9fed1dd58&t=1554278616" },
