          { name:'kyloRen',text: '凯洛·伦',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/145px-Star_Wars_The_Force_Awakens_Kylo_Ren.jpg?sign=37635c10fd2f3fff34650853306606b8&t=1554278352' },
          { name:'darthMaul',text: '达斯·摩尔',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/109px-Darth_Maul.png?sign=d88243d7d46adbd644e9a6bc1280039f&t=1554279295' },
          { name:'countDooku',text: '杜库伯爵',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/194px-Count_Dooku.png?sign=c3599555d40ef44353a6200aff318242&t=1554281091' },
          { name:'generalGrievous',text: '格里弗斯将军',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/GeneralGrievous.JPG?sign=b2dcc4fda8db0adcb58de4bfd9a6ab47&t=1554281128' },
