          { name:'galacticEmpire',text: '银河帝国',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/145px-Emblem_of_the_First_Galactic_Empire.svg.png?sign=6c59a27ecf21a2c207fe70ed0f06c98a&t=1554278535' },
