          { name:'jediOrder',text: '绝地武士团',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/jedi.jpg?sign=908d5208d3adaf7c0c36e0bf6c0a91f5&t=1554278765' },
