          { name:'resistance',text: '抵抗组织',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/145px-Rebel_Alliance_logo.svg.png?sign=8ee19cfff869e33727b75cb9c36b05f7&t=1554279209' },
          { name:'newRepublic',text: '新共和国',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/182px-NewRepublicCanon-DelRey.png?sign=371afaad3be9b58e245a1c8f17e640af&t=1554278496' },
          { name:'rebelAlliance',text: '义军同盟',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starWar/145px-Rebel_Alliance_logo.svg.png?sign=de7ddd1f9400d18b9863d0a549cc3087&t=1554278341' },
