      footer: {
        inset: false
      },

     
    }),

    methods:{
      selectPath(name){ 
        var clickName=null;
         var _this=this;
        _this.clickName=name;
        _this.$router.push({path:'/starWar/'+name});
      },
       selectBar(name){ 
        var clickName=null;
         var _this=this;
        _this.clickName=name;
        _this.$router.push({path:'/'+name});
      },
    }
 
  
     
    
    
    
  }
</script>

<style>

.v-title-position{
  margin-left: 32px;
}
.v-icon-position{
  margin-left: 0px;
}
.list-position{
  margin-top: 60px;
}
.content-position-starWar{
  font-size: 20px;
  margin-left: 10px;
}
.avatar-position{
  margin-left: 45px;
}
.item-postition{
  margin-top:0px;
}
.v-list__tile--avatar{
