<template>
  <v-app id="starTerk" >
    <v-navigation-drawer 
      permanent
      overflow
      app
      stateless
    >

      <v-list class="list-position">
        <v-img
        src="https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/Star_Trek_logo_ultimate.jpg?sign=cb2ec1dd19033223b18dc9c7450daa11&t=1554029097"
      />    
      
        

 <hr class="mt-3 v-divider theme--light" >

        
            <v-list-tile
