    data: () => ({
            dialog: false,
      itemsHero: [
          { name:'kirk', text: '柯克',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/kirk.jpg?sign=d4cd787c6d8f168624fa8fd13c76c8cb&t=1555340542' },
          { name:'spock',text: '斯波克',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/spock.jpg?sign=d98d8a41ac132391d14fa4de8aebb7af&t=1555340571' },
          { name:'mcCoy',text: '麦考伊',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/mcCoy.jpg?sign=ed72939210b34a88560b509f347f758e&t=1555340559' },
          { name:'scotty',text: '斯科特',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/scotty.jpg?sign=18b07f7a569544b78ffd12e08de2f144&t=1555340617' },
          { name:'uhura',text: '乌胡拉',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/uhura.jpg?sign=bc1b55fd226199c59dbbc578a7f6b0b8&t=1555340600' },
          { name:'sulu', text: '苏鲁',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/sulu.jpg?sign=64dc0436c6f541d6d0b7b1f9acb9acca&t=1555340582' },
          { name:'chekov',text: '契科夫',avatar: 'https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/starTerk/chekov.jpg?sign=c5b36a160bbe523b0bbb9a58b8d1c3a9&t=1555340516' },
