          { name:'marvel/marvelPreface',text:'漫威'},
          { name:'DC/DCPreface',text:'DC'},
          { name:'starWar/starWarPreface',text:'星球大战'},
          { name:'starTerk/starTerkPreface',text:'星际迷航'},
        ],
        
        
      footer: {
        inset: false
      },

     
    }),

    methods:{
      selectPath(name){ 
        var clickName=null;
         var _this=this;
        _this.clickName=name;
        _this.$router.push({path:'/starTerk/'+name});
      },
     selectBar(name){ 
        var clickName=null;
         var _this=this;
        _this.clickName=name;
        _this.$router.push({path:'/'+name});
      },
    }
 
  
     
    
    
    
  }
</script>

<style>

.v-title-position{
  margin-left: 32px;
}
.v-icon-position{
  margin-left: 0px;
}
.list-position{
  margin-top: 60px;
}
.content-position-starTerk{
  font-size: 20px;
  margin-left: 10px;
}
.item-postition{
  margin-top:0px;
}
.v-list__tile--avatar{
  height: 48px !important;
}

</style>
